import './App.css';
import happy from "./happy_song.json";
import sad from "./sad_song.json";
import anger from "./anger_song.json";
import React, { useState } from 'react';
import happyface from './img/happy-face.png';
import angryface from './img/angry-face.png';
import sadface from './img/sad-face.png';

function App() {
  const[song, setSong] = useState('');
  const[popup, setPopup] = useState(false);
  function getRandomInt(max) {
    return Math.floor(Math.random() * max);
  }
  function getHappySong(){
    var numSongs = Object.keys(happy.title).length;
    var randomNum = getRandomInt(numSongs);
    setSong(happy.title[randomNum])
  }
  function getSadSong(){
    var numSongs = Object.keys(sad.title).length;
    var randomNum = getRandomInt(numSongs);
    setSong(sad.title[randomNum])
    //alert(sad.title[randomNum]);
  }
  function getAngerSong(){
    var numSongs = Object.keys(anger.title).length;
    var randomNum = getRandomInt(numSongs);
    setSong(anger.title[randomNum])
  }
  function popUp(){
    setPopup(!popup);
  }
  return (
    <div className="App">
      <h1>taylor swift songs for your mood</h1>
      <p id='explain'> I used the Genius API to get lyrics from the most popular Taylor Swift songs. I used NLP to analyze the lyrics and assign a emotional category. You can use the faces below to find a Taylor Swift song to match your mood.</p>
      <p class='song'>{song}</p> 
      <div class="square">
          <div class='vertical-center'> 
          <table> 
          <td>  <img onClick={getSadSong} src={sadface}/></td>
          <td> <img onClick={getHappySong} src={happyface}/> </td>
          <td> <img onClick={getAngerSong} src={angryface}/> </td>
          </table> 
        </div> 
      </div>
      <div> Icons made by <a href="https://www.freepik.com" title="Freepik">Freepik</a> from <a href="https://www.flaticon.com/" title="Flaticon">www.flaticon.com</a></div>
    </div>
  );
}

export default App;
